<template>
  <div class="about">
    <div class="section">
      <h2>Rewards</h2>
      <p>Check how much rewards your pets accrued and claim them.</p>
      <div v-if="$store.state.user" class="nes-container is-dark with-title">
        <p class="title">Check & Claim</p>
        <div style="background-color:#212529; padding: 1rem;" class="nes-field">
          <label for="dark_field" style="color:#fff;">Your Address</label>
          <input v-model="parent" type="text" id="dark_field" class="nes-input is-dark" placeholder="">
          <br>
          <div style="width: 100%; text-align: right">
            <button type="button" :class="'nes-btn is-primary ' + (disabled ? 'is-disabled' : '')" :disabled="disabled" @click="claim()">Claim</button>
            <button type="button" :class="'nes-btn ' + (disabled ? 'is-disabled' : '')" :disabled="disabled" @click="check()">Check</button>
          </div>
          <p v-if="result" style="text-align: right"> {{result}} Cudl</p>
        </div>
      </div>
      <connect v-else style="text-align:center; margin-top: 50px"></connect>
    </div>
  </div>
</template>
<script>
import Connect from '@/components/Connect.vue'

export default {
  components: {
    Connect
  },
  data(){
    return {
      parent: this.$store.state.user,
      result: null,
      loading: false,
    }
  },
  methods: {
    async check(){
      this.loading = true;
      try {
        this.result = this.parent ? await this.$store.dispatch("checkRewards", this.parent) : null
        this.result = this.result ? await this.$store.dispatch("toEth", this.result) : this.result
      }
      catch(err){
        console.error(err)
        alert(err["message"])
      }
      this.loading = false;
    },

    async claim(){
      this.loading = true;
      try{
        await this.$store.dispatch("claimRewards", this.parent)
      }
      catch(err){
        console.error(err)
        alert(err["message"])
      }
      this.loading = false;
    }
  },
  computed: {
    disabled() {
      if(this.loading){
        return true
      }
      if(!this.parent){
        return true
      }
    }
  }
}
</script>